.home {
    @apply bg-mainHeadingGradient pt-28 mobile:pt-5 relative;

    .image {
        @apply relative drop-shadow-colored mobile:w-[225px] mobile:h-[245px] ml-auto mobile:mr-4 mobile:-mt-2;
        will-change: filter;

        & + a {
            @apply mobile:absolute mobile:right-0 mobile:-left-[3px] mobile:bottom-[26px] mobile:max-w-[182px] mobile:mx-auto;
        }
    }
}

.title {
    @apply text-[56px] leading-[64px] font-bold font-secondary tracking-tight mobile:text-2xl mobile:leading-7 mobile:mt-5;
}

.subtitle {
    @apply notMobile:text-xl notMobile:leading-[30px] mt-5 mb-10 mobile:mt-3 mobile:mb-5 mobile:text-sm;
}

.benefits {
    @apply flex mobile:pt-[40px] mobile:mx-auto mobile:max-w-[332px] flex-wrap -mt-6 mobile:-mt-4;
}

.benefit {
    @apply w-[208px] max-w-[208px] mobile:max-w-[156px] min-h-[162px] bg-white text-center rounded-lg shadow-popUp p-6 whitespace-normal align-middle mobile:w-1/2 mobileXs:w-[208px] mobileXs:mx-auto mobileXs:max-w-[208px] mobileXs:mb-0 mt-6 mobile:mt-4;

    & + .benefit {
        @apply ml-6 mobileXs:ml-auto mobileXs:mt-4 mobile:ml-0;
    }

    &:nth-child(2n-1) {
        @apply notMobile:ml-auto;
    }

    &:nth-child(2n) {
        @apply mobile:ml-auto;
    }

    svg {
        @apply mb-2 mx-auto;
    }

    h4,
    span {
        @apply text-[#1E282D] static;
    }

    h4 {
        @apply font-semibold;
    }

    span {
        @apply text-[10px] inline-block font-medium;
    }
}

.advantages {
    @apply -ml-[60px] mobile:ml-0 -mt-[60px] mobile:mt-0 w-[804px] mobile:w-full;
    .benefit {
        @apply ml-[60px] mobile:ml-0 mt-[60px] mobile:mt-4 tablet:ml-auto;

        &:nth-child(2n) {
            @apply mobile:ml-auto;
        }
    }
}

.steps {
    @apply mt-10 mb-12 mobile:mb-6 mobile:mt-[592px] mobile:max-w-[302px] mobile:mx-auto;

    img {
        @apply absolute top-0 -left-[316px] opacity-0 transition-opacity ease-in-out duration-300 z-50 mobile:w-[280px] mobile:h-[562px] mobile:top-[72px] mobile:left-1/2 mobile:-translate-x-1/2 mobile:right-auto;
        will-change: filter;
        filter: drop-shadow(0px 0px 50px rgba(38, 50, 56, 0.1));

        @media (max-width: 767px) {
            filter: drop-shadow(0px 0px 50px rgba(38, 50, 56, 0.15));
        }
    }

    .overlay {
        @apply relative flex w-[80px] mobile:w-[68px] h-[80px] mobile:h-[68px] min-w-[80px] mobile:min-w-[68px] min-h-[80px] mobile:min-h-[68px] mr-8 mobile:mr-6 rounded-full;
    }

    svg {
        @apply mx-auto my-auto mobile:w-10 mobile:h-10;
    }

    h3 {
        @apply mb-2 text-2xl font-medium mobile:text-xl;
    }
}

.step {
    @apply flex transition-all;

    & + .step {
        @apply mobile:mt-[24px] mt-16;
    }

    &.active {
        .overlay {
            @apply transition ease-in-out bg-[#2A3266] duration-300;
        }

        svg {
            path {
                @apply transition ease-in-out duration-300 fill-white;
            }
        }

        img {
            @apply opacity-100;
        }
    }

    .overlay {
        @apply after:absolute after:-bottom-[56px] mobile:after:-bottom-[47px] after:left-1/2 after:w-[2px] after:h-[40px] after:bg-[#1E282D] after:rounded-full cursor-pointer;
    }
}

.step {
    &:last-child {
        .overlay {
            &:after {
                content: none;
            }
        }
    }
}
